import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Home from './pages/Home';
import Search from './components/Search';
import Album from './pages/Album';
import Favorites from './pages/Favorites';
import Profile from './pages/Profile';
import ProfileEdit from './pages/ProfileEdit';
import NotFound from './components/NotFound';
import Header from './components/Header';
import './index.css';
// import 'bootstrap/dist/css/bootstrap.min.css';

class App extends React.Component {
  render() {
    return (
      <BrowserRouter>
        <Route path="/projects/trybetunes/*">
          <header className="d-flex align-items-center justify-content-around bg-info p-4 rounded-pill m-5">
          <h1 className="glyphicon glyphicon-headphones"> </h1>
            <h1 className="m-3">
              TrybeTunes
            </h1>
          </header>
        </Route>
        <Route path="/projects/trybetunes/search" component={ Header } />
        <Route path="/projects/trybetunes/Album" component={ Header } />
        <Route path="/projects/trybetunes/favorites" component={ Header } />
        <Route path="/projects/trybetunes/profile/edit" component={ Header } />
        <Switch>
          <Route exact path="/projects/trybetunes/" component={ Home } />
          <Route path="/projects/trybetunes/search" component={ Search } />
          <Route path="/projects/trybetunes/album/:id" component={ (props) => (<Album { ...props } />) } />
          <Route path="/projects/trybetunes/favorites" component={ Favorites } />
          <Route exact path="/projects/trybetunes/profile" component={ Profile } />
          <Route path="/projects/trybetunes/profile/edit" component={ ProfileEdit } />
          <Route path="/projects/trybetunes/*" component={ NotFound } />
        </Switch>
      </BrowserRouter>
    );
  }
}

export default App;
