import React from 'react';
import { Route, Switch } from 'react-router';
import { Provider } from 'react-redux';
import { BrowserRouter } from 'react-router-dom';
import Login from './pages/Login';
import store from './store';
import Game from './pages/Game';
import Settings from './pages/Settings';
import Feedback from './pages/Feedback';
import Ranking from './pages/Ranking';
import './index.css';
// import 'bootstrap/dist/css/bootstrap.min.css';

export default function App() {
  return (
    <Provider store={ store }>
      <BrowserRouter>
        <Switch>
          <Route path="/projects/trivia/feedback" component={ Feedback } />
          <Route path="/projects/trivia/game" component={ Game } />
          <Route path="/projects/trivia/settings" component={ Settings } />
          <Route path="/projects/trivia/ranking" component={ Ranking } />
          <Route path="/projects/trivia/" component={ Login } />
        </Switch>
      </BrowserRouter>
    </Provider>
  );
}
