import React from 'react';
import { Provider } from 'react-redux';
import { Switch, Route } from 'react-router-dom';
// import 'bootstrap/dist/css/bootstrap.min.css';
import Login from './pages/Login';
import store from './redux/store/index';
import ComidasOuBebidas from './pages/ComidasOuBebidas';
import InProgress from './pages/InProgress';
import Explorar from './pages/Explorar';
import ExplorarBebidasOuComidas from './pages/ExplorarBebidasOuComidas';
import ExplorarIngredientes from './pages/ExplorarIngredientes';
import ExplorarComidasArea from './pages/ExplorarComidasArea';
import Perfil from './pages/Perfil';
import ReceitasFavoritas from './pages/ReceitasFavoritas';
import ReceitasFeitas from './pages/ReceitasFeitas';
import Details from './pages/Details';
import NotFound from './pages/NotFound';
import ProviderHook from './services/Provider';
import './index.css';

function App() {
  return (
    <Provider store={ store }>
      <ProviderHook>
        <Switch>
          <Route exact path="/projects/drinks-and-foods" component={ Login } />
          <Route path="/projects/drinks-and-foods/comidas/:id/in-progress" component={ InProgress } />
          <Route path="/projects/drinks-and-foods/bebidas/:id/in-progress" component={ InProgress } />
          <Route path="/projects/drinks-and-foods/comidas/:id" component={ Details } />
          <Route path="/projects/drinks-and-foods/bebidas/:id" component={ Details } />
          <Route path="/projects/drinks-and-foods/comidas" component={ ComidasOuBebidas } />
          <Route path="/projects/drinks-and-foods/bebidas" component={ ComidasOuBebidas } />
          <Route path="/projects/drinks-and-foods/explorar/comidas/area" component={ ExplorarComidasArea } />
          <Route path="/projects/drinks-and-foods/explorar/bebidas/area" component={ NotFound } />
          <Route
            path="/projects/drinks-and-foods/explorar/bebidas/ingredientes"
            component={ ExplorarIngredientes }
          />
          <Route
            path="/projects/drinks-and-foods/explorar/comidas/ingredientes"
            component={ ExplorarIngredientes }
          />
          <Route path="/projects/drinks-and-foods/explorar/bebidas" component={ ExplorarBebidasOuComidas } />
          <Route path="/projects/drinks-and-foods/explorar/comidas" component={ ExplorarBebidasOuComidas } />
          <Route path="/projects/drinks-and-foods/explorar" component={ Explorar } />
          <Route path="/projects/drinks-and-foods/perfil" component={ Perfil } />
          <Route path="/projects/drinks-and-foods/receitas-feitas" component={ ReceitasFeitas } />
          <Route path="/projects/drinks-and-foods/receitas-favoritas" component={ ReceitasFavoritas } />
          <Route path="/projects/drinks-and-foods/*" component={ NotFound } />
        </Switch>
      </ProviderHook>
    </Provider>

  );
}

export default App;
