import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import Header from '../components/Header';
import ShareButton from '../components/ShareButton';
import '../styles/receitasFeitas.css';

function renderUrl(type, id) {
  const URL = window.location.href.replace('/receitas-feitas', '');
  const TYPE = `${type}s`;
  return `${URL}/${TYPE}/${id}`;
}

function ReceitasFeitas() {
  const [categories, setCategories] = useState('All');
  const doneRecipes = JSON.parse(localStorage.getItem('doneRecipes'));
  const [hasDoneRecipes, toggleHasDoneRecipes] = useState(false);

  useEffect(() => {
    if (doneRecipes) toggleHasDoneRecipes(true);
  }, []);

  return (
    <div className="receitas-feitas-page">
      <Header pageTitle="Receitas Feitas" />
      <div className="buttons-filter">
        <button
          type="button"
          data-testid="filter-by-all-btn"
          onClick={ () => setCategories('All') }
        >
          All
        </button>
        <button
          type="button"
          data-testid="filter-by-food-btn"
          onClick={ () => setCategories('comida') }
        >
          Foods
        </button>
        <button
          type="button"
          data-testid="filter-by-drink-btn"
          onClick={ () => setCategories('bebida') }
        >
          Drinks
        </button>
      </div>
      { hasDoneRecipes
        ? doneRecipes.filter((recipe) => {
          if (categories === 'All' || recipe.type === categories) return true;
          return false;
        }).map((recipe, index) => (
          <div key={ recipe.id } className="card-item">
            <Link
              to={ (recipe.type === 'comida'
                ? `/projects/drinks-and-foods/comidas/${recipe.id}` : `/projects/drinks-and-foods/bebidas/${recipe.id}`) }
            >
              <img
                data-testid={ `${index}-horizontal-image` }
                src={ recipe.image }
                alt={ recipe.name }
                className="horizontal-image"
              />
            </Link>
            <div>
              <div className="categories-text">
                <p
                  data-testid={ `${index}-horizontal-top-text` }
                >
                  { `${recipe.area || recipe.alcoholicOrNot} - ${recipe.category}` }
                </p>
                { recipe.tags && recipe.tags.map((tag, i) => (
                  <p
                    data-testid={ `${index}-${tag}-horizontal-tag` }
                    key={ i }
                  >
                    {tag}
                  </p>
                ))}
              </div>
              <div>
                <Link
                  className="card"
                  to={ (recipe.type === 'comida'
                    ? `/projects/drinks-and-foods/comidas/${recipe.id}` : `/projects/drinks-and-foods/bebidas/${recipe.id}`) }
                >
                  <p
                    className="recipes-text"
                    data-testid={ `${index}-horizontal-name` }
                  >
                    { recipe.name }
                  </p>
                </Link>
                <p
                  className="date"
                  data-testid={ `${index}-horizontal-done-date` }
                >
                  { recipe.doneDate }
                </p>
                <ShareButton
                  index={ index }
                  url={ renderUrl(recipe.type, recipe.id) }
                />
              </div>
            </div>
          </div>
        ))
        : <h1>Voc no tem receitas feitas ainda!</h1>}
    </div>
  );
}

export default ReceitasFeitas;
